.player {
  display: grid;
  grid-template-columns: 1fr -webkit-max-content;
  grid-template-columns: 1fr max-content;
  margin: 4px 0 8px 0;
  border: 1px solid rgba(0, 0, 0, 0.3);
  padding: 8px 8px;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
          box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
}

.player__username,
.player__username--accepted {
  font-weight: bold;
  margin: 0;
}

.player__username {
  text-shadow: 0 0 12px var(--red);
}

.player__username--accepted {
  text-shadow: 0 0 12px rgb(0, 255, 0);
}

.player__creator-title {
  margin-bottom: 2px;
}

.player__btn {
  font-weight: bold;
  border: none;
  border-radius: 20px;
  padding: 6px 10px;
  background-color: red;
  color: white;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.3);
  text-align: center;
}


